@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin:0;
}
.App {
  text-align: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  margin: auto;
}

.spinner-border {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 300px;
  height: 300px;
  border-width: 50px;
}


.header {
  width: 100%;
  position: relative;
  top: 0;
  background: #F2F6F6;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-items: center;
  padding: 1%;
}

.header h1 {
  margin: 1%;
}


.main {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;

}

.placeholder {
  font-size: 60px;
  margin: 20px;
}

.guess {
  width: 153px;
  height: 41px;
  border-radius: 15px;
}

.btn-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.stat-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 25px;
}

.stat-container p {
  margin: 0;
}
.stat-container .btn:nth-child(2) {
  width: 100%;
  height: 50%;
  font-size: 1.5vw;
}

.description-container {
  width: 70%;
  transition: opacity 300ms ease;
  height: 100%;
  overflow: scroll;
}

.heart {
  width: 30px;
  height: 30px;
  color: #664FA7;
}
.pop-up {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    height: 28vw;
    width: 60vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 5px 5px 15px -5px #000000;
    background: #ffffff;
    border-radius: 15px;
  }



.x {
    position: absolute;
    top: 5%;
    right: 3%;
    height: 19%;
    width: 8%;
    cursor: pointer;
}
